.backdrop {
  background-color: var(--fourth-C2E297);
  position: fixed;
  width: 100vw;
  height: 100vh;
  overflow: scroll;
  top: 0;
  display: none;
  transition: rotate 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal {
  position: absolute;
  width: 100%;
  max-height: 90vh;
  background-color: #e17992;
  border-radius: 4px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding-top: 15px;
  @media screen and(min-width:480px) {
    max-width: 380px;
    min-height: 90vh;
  }
}

.modal__close {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  position: absolute;
  cursor: pointer;
  color: var(--eighth992);
  border: 1px solid var(--eighth992);
  background-color: var(--white);
  transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
  top: 15px;
  right: 15px;
  &:hover,
  &:focus {
    background-color: var(--eighth992);
    color: #ffffff;
    border: 1px solid #ffffff;
  }
}
.form {
  padding: 30px;
}

.modal__svg {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  fill: currentColor;
}
.modal__title {
  text-align: center;
  margin-top: 0;
}
.form-div {
  position: relative;
  margin-bottom: 10px;
}
.form__label {
  display: block;
  margin-bottom: 5px;
}

.form__input {
  display: block;
  width: 100%;
  height: 40px;
  border: transparent;
  padding-left: 35px;
  outline: none;
}
.form__select {
  display: block;
  width: 100%;
  height: 40px;
  border: transparent;
  padding-left: 30px;
}
.form-svg {
  position: absolute;
  top: 35px;
  left: 10px;
  fill: var(--eighth992);
  width: 20px;
  height: 20px;
}

.form__aria {
  display: block;
  width: 100%;
  height: 80px;
  margin-top: 15px;
  resize: none;
  padding-left: 30px;
  font-size: 18px;
  border: transparent;
}

.btn {
  min-width: 110px;
  height: 40px;
  background: #ffffff;
  border-radius: 20px;
  border-color: transparent;
  margin-top: 20px;
  margin-right: auto;
  margin-left: auto;
  font-family: 'DM Sans';
  font-weight: 400;
  font-size: 18px;
  cursor: pointer;
  line-height: calc(18 / 14);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #d41443;
  transition-property: box-shadow, color, background-color;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 250ms;
  &:hover,
  &:focus {
    background: #c7133e;
    color: #ffffff;
  }
}
