.header {
  position: absolute;
  width: 100%;

  padding-top: 3px;

  z-index: 9;

  @media screen and (min-width: 768px) {
    padding-top: 37px;
  }
}

.container.header__container {
  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding-right: 115px;
    padding-left: 46px;
  }
}

.header-img {
  display: block;
}
.btn-mob-menu {
  padding: 0;

  background-color: transparent;
  border: none;

  @media screen and (min-width: 768px) {
    margin-left: auto;
  }

  @media screen and (min-width: 1280px) {
    display: none;
  }
}

.header-container--mobile {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media screen and (min-width: 768px) {
    justify-content: flex-start;
  }
}

.mobile-conteiner--menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 250px;
  height: 100vh;
  z-index: 9;

  @media screen and (min-width: 768px) {
    width: 40vw;
  }

  background-color: #e17992;

  padding-top: 23px;
  padding-left: 20px;
  padding-right: 20px;

  transform: translateX(120%);

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

  opacity: 0;

  visibility: hidden;

  @media screen and (min-width: 1280px) {
    display: none;
  }

  &.is-open {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
  }
}

.mobile__btn-close {
  display: block;
  margin-left: auto;
  background-color: transparent;
  border: none;

  cursor: pointer;

  margin-bottom: 23px;
}

.header-btn {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;

  color: #d41443;

  background-color: #ffffff;

  width: 209px;
  height: 44px;
  border-radius: 22px;
  border: none;
  margin-bottom: 19px;
  padding: 0;

  @media screen and (min-width: 768px) {
    width: 160px;
  }
}

.header-btn-tab {
  display: none;
  @media screen and (min-width: 768px) {
    display: block;
    margin-bottom: 0;
    margin-left: 30px;

    &:hover,
    &:focus {
      background-color: #ecebeb;
    }
  }
}

.header__nav {
  display: none;
  @media screen and (min-width: 1280px) {
    display: block;
  }
}

.header__menu-list {
  padding: 0;
  margin: 0;
}

.header__menu-item {
  border-bottom: 1px solid #e18298;
  border-height: 40px;
}

.header__menu-link {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;

  color: #ffffff;

  display: block;

  padding-top: 11px;
  padding-bottom: 13px;

  &:hover,
  &:focus {
    color: #d41443;
  }
}

.header__nav-list {
  list-style: none;
  padding: 0;
  margin: 0;

  display: flex;

  margin-left: 110px;
}

.header__nav-item:not(:last-child) {
  margin-right: 35px;
}

.header__nav-link {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;

  color: #ffffff;

  display: block;
  text-decoration: none;
  padding: 15px 0;

  &:hover,
  &:focus {
    color: #d41443;
  }
}
